body {
  padding: 2em;
  margin: auto;
}

h1 {
  color: #262626;
  font-family: "Georgia", serif;
  font-size: 44px;
  line-height: 48px;
  margin: auto auto 30px;
  max-width: 40rem;
  margin-top: 50vh;
  font-weight: 100;
}

hr {
  border: none;
  height: 1px;
  background-color: #ddd;
  margin: auto auto 5rem;
  max-width: 40rem;
  margin-top: 5rem;
  margin-bottom: 5rem;
}

h2 {
  color: #262626;
  font-family: "Georgia", serif;
  font-size: 30px;
  line-height: 48px;
  margin: auto auto 30px;
  max-width: 40rem;
  margin-top: 50vh;
  letter-spacing: 4px;
  font-weight: 100;
}

p {
  color: #2f2f2f;
  font-family: "New York", serif;
  font-size: 17px;
  line-height: 28px;
  margin: auto auto 24px;
  max-width: 40rem;
}
